import { Link, useLocation } from 'react-router-dom'
import styled from 'styled-components'

const H1Link = styled(Link)`
  font-family: 'Netflix Sans', sans-serif;
  font-weight: 600;
  font-size: 70px;
  margin: 20px;
`

const Header = styled.header`
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
  background-color: rgba(20, 20, 20, 0.8);
  z-index: 10;
  box-shadow: 0px 1px 5px 2px rgba(0, 0, 0, 0.8);
`

const List = styled.ul`
  display: flex;
  align-items: right;
`

type propType = {
  current: boolean
}

const Item = styled.li`
  width: 80px;
  height: 50px;
  text-align: center;
  border-bottom: 3px solid ${(props: propType) => (props.current ? 'white' : 'transparent')};
  transition: border-bottom 0.5s ease-in-out;
`
const SLink = styled(Link)`
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
`

const Location = () => {
  const location = useLocation()
  return (
    <Header>
      <H1Link to="/">LIMFLIX</H1Link>
      <List>
        <Item current={location.pathname === '/'}>
          <SLink to="/">Movies</SLink>
        </Item>
        <Item current={location.pathname === '/tv'}>
          <SLink to="/tv">TV</SLink>
        </Item>
        <Item current={location.pathname === '/search'}>
          <SLink to="/search">Search</SLink>
        </Item>
      </List>
    </Header>
  )
}

export default Location
